import React, {useState} from 'react';

export type ControlledRatingType = 0 | 1 | 2 | 3 | 4 | 5

export type ControlledRatingValueType = {
    value: ControlledRatingType
    onClick: (value: ControlledRatingType) => void
}

export function Rating(props: ControlledRatingValueType) {

    const SetRatingHandler = (starValue: 1 | 2 | 3 | 4 | 5) => {

    };

    return (
        <div>
            <Star selected={props.value > 0} onClick={props.onClick} value={1}/>
            <Star selected={props.value > 1} onClick={props.onClick} value={2}/>
            <Star selected={props.value > 2} onClick={props.onClick} value={3}/>
            <Star selected={props.value > 3} onClick={props.onClick} value={4}/>
            <Star selected={props.value > 4} onClick={props.onClick} value={5}/>
        </div>
    );

}

type StarPropsType = {
    selected: boolean
    onClick: (value: ControlledRatingType) => void
    value:ControlledRatingType
}

export function Star(props: StarPropsType) {
    return <span onClick={()=>props.onClick(props.value)}>{props.selected ? <b> star</b> : ' star'}</span>;
}
